.app__certification{
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__certification-item
{
    width: 60%;
    min-height: 320px;
    background-color: var(--white-color);
    display: flex;
    padding: 2rem;
    border-radius: 20px;

    box-shadow: 0 0 25px rgba(0,0,0,0.1);
    transition: all 0.3s ease-in-out;

    img{
        width: 200px;
        height: 150px;
        border-radius: 15px;
        object-fit: cover;
    }

    @media screen and(min-width:2000px) {
        min-height: 450px;

        img{
            width: 150px;
            height: 150px;
        }
        
    }

    @media screen and(max-width:800px) {
        width: 100%; 
    }

    @media screen and(max-width:600px) {
       flex-direction: column; 
    }

}

.app__certification-content
{
    flex: 1;
    height: 100%;
    padding: 0 2rem;
    text-align: left;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;

    p{
        font-size: 1.25rem;
        line-height: 2rem;
        color: var(--black-color);
        font-family: var(--font-base);
    }

    h4{
        font-weight: 600;
        color: var(--secondary-color);
        margin-top:2rem;
    }

    h5{
        font-weight: 400;
        color: var(--grey-color);
        margin-top: 5rem;
    }
}
.app__certification-btns
{
    flex-direction: row;
    margin-top: 1rem;

    div{
        width: 50px;
        height: 50px;
        border-radius: 50px;
        background-color: var(--primary-color);
        margin: 1rem;
        transition: all 0.3s ease-in-out;

        svg{
            width: 20px;
            height: 20px;
            color: var(--secondary-color);
        }
         &:hover{
                    background-color: var(--secondary-color);
                    svg{
                        color: var(--white-color);
                    }
            }
        }
    }
.app__certification-brands
{
    width: 80%;
    flex-wrap: wrap;
    margin-top: 2rem;

    div{
        width: 150px;
        margin: 1.5rem;

        img{
            width: 100%;
            height: auto;
            object-fit: cover;
            filter: grayscale(1);
        }

        &:hover{
            img{
                filter: grayscale(0);
            }
        }

        @media screen and (min-width:2000px) {

            width: 210px;
            margin: 2rem;
            
        }

        @media screen and (max-width:450px) {

            width: 120px;
            margin: 1rem;
            
        }
    }
    @media screen and (max-width:800px) {

        width: 100%;

        
    }
}