.app__navbar
{
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: rem 2rem;

    background:rgba(255,255,255,0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);

    border: 1px solid rgba(255,255,255,0.18);
    position:fixed;
    z-index: 2;
}

.app__navbar-logo
{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: -2%;

   img{
    width:70px;
    height:50px
   }
}


.app__navbar-link
{
    flex: 1;
    display: flex;
    justify-content: center;
   align-items: center;
   
   list-style: none;

   li{ 
    margin: 0 1rem;
    cursor: pointer;

    flex-direction: column;
   
    div 
    {
        width: 100%;
        height: 2px;
        background: transparent;
        transition: all 0.3s ease-in-out;

        margin-bottom: 5px;
    }
    a{
        color: var(--gray-color);
        text-decoration: none;
        flex-direction: column;
        text-transform: uppercase;
        font-size: 500;
        transition: all 0.3s ease-in-out;

        &:hover
        {
            color: var(--secondary-color);
        }
    }

    &:hover
    {
        div
        {
            background-color: var(--secondary-color);
        }
    }

   };

   @media  screen and(max-width:750px) {

    display: none;
    
   }

}

.app__navbar-menu
{
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secondary-color);


    svg{
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }

    div{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;
        padding: 1rem;
        width: 50%;
        height: 100vh;
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        flex-direction: column;
        background:url(../../assets/icons8-react.svg);
        background-size:100px 100px;
        background-color:var(--white-color) ;
        background-size: cover;
        background-repeat: repeat;


        box-shadow: 0 0 20px  rgba(168,168,168,0.15);

        svg{
            width: 30px;
            height: 30px;
            color: var(--secondary-color);
            margin: 0.5rem 1rem;
        }

        ul{
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            li{
                margin: 1rem;
                a{
                    color: var(--gray-color);
                    text-decoration: none;
                    font-size: 1rem;
                    text-transform: uppercase;
                    font-weight: 500;
                    transition: 0.3s ease-in-out;
                    
                    &:hover
                        {
                            color: var(--secondary-color);
                        }
                }
            }
        }
        @media screen and(min-width:750px) {
            display: none;
        }
    }
    @media screen and(min-width:750px) {
        display: none;
    }
}
