#home
{
    position: relative;
    background: url('../../assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;

    .app__wrapper
    {
        padding:0;

        .copyright{
            display: none;
        }
    }
}


.app__header
{
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;

    padding: 6rem 2rem 0;

    @media screen and (min-width:2000px) {
        padding-top: 8rem;
    }

    @media screen and(max-width:1200px) {
        flex-direction: column;
    }
    @media screen and (max-width:400px) {
        padding: 6rem 1rem 2rem;
    }
}

.app__header-info
{
    flex:  0.7;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
  


    @media screen and(max-width:2000px) {
        
        width: 100%;
        margin-right: 0rem; 
    }
    @media screen and(max-width:768px) {
        
        width: 100%;

    }
}

.app__header-badge
{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp,.tag-cmp
    {
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;

        box-shadow: 0 0 20px rgba(0,0,0,0.1);
    }
    .tag-cmp
    {
        flex-direction: column;
        margin-top: 3rem;

        p{
            width: 100%;
            text-transform: uppercase;
            text-align: left;
        }
    }

    span{
        font-size: 2.5rem;

        @media screen and(min-width:2000px) {
            font-size: 5rem;
        }
    }
    @media screen and(max-width:1200px) {
        justify-content: flex-start;
        align-items: flex-start; 
    }
}

.app__header-circles
{
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;

div{
    width: 100px;
    height: 100px;
    border-radius: 50%;

    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0,0,0,0.1);

    img {
        width: 70%;
        height:70%;
    }
}

div:nth-child(1)
{
    width: 100px;
    height: 100px;
}

div:nth-child(2)
{
    margin: 2rem;
    width: 250px;
    height: 250px;
}

div:nth-child(3)
{
    width: 150px;
    height: 150px;
}
div:nth-child(4)
{
    margin-left: -30rem;
    margin-top: -40rem;
    width: 100px;
    height: 100px;
}
div:nth-child(5)
{
    margin-left: -35rem;
    margin-top: 25rem;
    width: 100px;
    height: 100px;
}




@media screen and(max-width:1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div{
        margin: -3rem;
    }
    
}

@media screen and (max-width:1150px) {

    display: none;
}

}
.app__header-img
{
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;

    img{
        width: 100%;
        z-index: 1;

    }

    .overlay_circle
    {
        position:absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 80%;
    }

    @media screen and (max-width:1200px) {
        margin: 1rem 0;
    }
}





